                [120.391722,36.067596,"地址：五四广场<h4><a target='_blank' href='http://000.00.00.000:49154' style='color:blue;'>DDDD</a></h4>"],
                [120.386296,36.06734,"地址：东海路 <h4><a target='_self' href='http://000.00.00.000:49100' style='color:blue;'>EEEE</a></h4>"],
                [120.356041,36.071111,"地址：中山公园<br/><a target='_self' href='http://000.00.00.000:49100'>4月2日，山东青岛天气转暖，中山公园内的樱花终于迎来盛花期，数百株樱花树争相绽放。并且第三届樱花节也正式启动。</a><br/><a target='_self' href='http://000.00.00.000:49100'>热度：63</a>"],
				[120.396317,36.102139,"地址：辽阳路<br/><a target='_self' href='http://000.00.00.000:49100'>辽阳路，有两车并排剐蹭。目前已堵至福辽立交桥下</a><br/><a target='_self' href='http://000.00.00.000:49100'>热度：63</a>"],

            ];

			// 添加事件影响范围
			for(var i=0;i<data_info.length;i++){
				if(i!=data_info.length-1){
					var circle = new BMap.Circle(new BMap.Point(data_info[i][0],data_info[i][1]),(i+1)*100,{fillColor:"red", strokeWeight: 1 ,fillOpacity: 0.3, strokeOpacity: 0.3});  // 创建标注
				}
				else{
					var circle = new BMap.Circle(new BMap.Point(data_info[i][0],data_info[i][1]),1300,{fillColor:"red", strokeWeight: 1 ,fillOpacity: 0.3, strokeOpacity: 0.3});  // 创建标注
				}
                map.addOverlay(circle);               // 将标注添加到地图中
            }
			var opts = {
                min_width : 250,     // 信息窗口zuixiao宽度
                min_height: 80,     // 信息窗口高度
                title : "详细信息" , // 信息窗口标题
                enableMessage:true//设置允许信息窗发送短息
            };
            for(var i=0;i<data_info.length;i++){
                var marker = new BMap.Marker(new BMap.Point(data_info[i][0],data_info[i][1]));  // 创建标注
                var content = data_info[i][2];
                map.addOverlay(marker);               // 将标注添加到地图中
                addClickHandler(content,marker);
            }
			function addClickHandler(content,marker){
                marker.addEventListener("click",function(e){
                    openInfo(content,e)}
                );
            }
            function openInfo(content,e){
                var p = e.target;
                var point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
                var infoWindow = new BMap.InfoWindow(content,opts);  // 创建信息窗口对象 
                map.openInfoWindow(infoWindow,point); //开启信息窗口
            }
            
            //向地图中添加缩放控件
            var ctrl_nav = new BMap.NavigationControl({anchor:CUSTOM_MAP_CONFIG,type:CUSTOM_MAP_CONFIG});
            map.addControl(ctrl_nav);
            //向地图中添加缩略图控件
            var ctrl_ove = new BMap.OverviewMapControl({anchor:CUSTOM_MAP_CONFIG,isOpen:1});
            map.addControl(ctrl_ove);
            //向地图中添加比例尺控件
            var ctrl_sca = new BMap.ScaleControl({anchor:CUSTOM_MAP_CONFIG});
            map.addControl(ctrl_sca);

			},
			
		}
	}
</script>

