<template>
  <div class="contains">
    <div id="amapall" class="all-amap">
      	<el-amap-search-box
        :visible="visible"
        @select="selectPoi"
        @choose="choosePoi"
      	/>
        <el-button icon="el-icon-search" type="primary" @click="openFullScreen1" v-loading.fullscreen.lock="fullscreenLoading" style="position: absolute;z-index: 999;left: 270px;top: 3px;">搜索</el-button>
        <div class="road_table_box">
				<div class="road_table_title_box">可优化路口列表</div>
				<div class="table_box_gap"></div>
        <el-table class="table_box"
          :data="tData"
          :row-class-name="tableRowClassName"
          @row-click="Click">
          <el-table-column
            prop="road"
            label="路口名称">
          </el-table-column>
          <el-table-column
            prop="result"
            label="优化原因">
          </el-table-column>
          <el-table-column
            prop="type"
            label="路口类型">
        </el-table-column>
        <el-table-column
            prop="button"
            label="生成方案"><el-button type="primary" size="mini" @click="openFullScreen2" icon="el-icon-document-checked" style="position: absolute;z-index: 999;top: 10px;">生成</el-button>
        </el-table-column>
  </el-table>
          <el-drawer
          title="君峰路_金口路优化方案："
          :visible.sync="fa_visible"
          :append-to-body="true"
          direction="rtl"
          :before-close="handleClose"
          size="50%">
          <el-image
          :src="imgUrl"
          fit="contain"></el-image>
          <el-image
          :src="imgUrl2"
          fit="fit"></el-image>
          <h5 style="margin-top: 15px;margin-left: 15px;font-family:Helvetica;">路口相位绿信比</h5>
          <el-divider></el-divider>
          <el-table :data="gridData">
              <el-table-column property="type" label="相位名称" width="150"></el-table-column>
              <el-table-column property="old_time" label="原绿灯时间" width="200"></el-table-column>
              <el-table-column property="new_time" label="优化绿灯时间"> 
                <template slot-scope="scope"> 
                <el-input-number
                  v-model="scope.row.new_time"
                  size="small"
              @change="handleChange(scope.row)"
            />
          </template>
              </el-table-column>
              <el-table-column property="change_time" label="调整时间"></el-table-column>
          </el-table>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>预计优化效果</span>
            </div>
            <div>
              <el-row :gutter="20">
                <el-col :span="6">
                  <div>
                    <el-statistic title="路口平均延误率下降">
                      <template slot="formatter"> 15.09% </template>
                    </el-statistic>
                  </div>
                </el-col>
                <el-col :span="6">
                  <div>
                    <el-statistic title="绿损平均下降">
                      <template slot="formatter"> 22.81% </template>
                    </el-statistic>
                  </div>
                </el-col>
                <el-col :span="6">
                </el-col>
              </el-row>
            </div>
          </el-card>
      <div style="margin-top: 20px">
          <el-dropdown split-button type="primary" @click="handleClick">
  绿信比下发
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item>绿波方案优化</el-dropdown-item>
    <el-dropdown-item>人工调整优化</el-dropdown-item>
    <el-dropdown-item>优化报告生成</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
<el-button type="primary" icon="el-icon-circle-check" style="margin-left: 20px;">执行</el-button>
      </div>
        </el-drawer>
        </div>
    </div>
  </div>
</template>

<script>
import { lazyAMapApiLoaderInstance } from "vue-amap";
import { Loading } from 'element-ui';
export default {
  data() {
    return {
      like: true,
        value1: '15.09%',
        value2: '22.81',
        title: '路口平均延误率下降',
      num: 16,
      imgUrl:require('D:\\upc\\match\\cs\\kuangjia\\sunon_web-main\\sunon_web-main\\src\\assets\\system\\trafficpos.png'),
      imgUrl2:require('D:\\upc\\match\\cs\\kuangjia\\sunon_web-main\\sunon_web-main\\src\\assets\\system\\traffictai.png'),
      fullscreenLoading: false,
      fa_visible: false,
      map: {},
	    zoom: 12,
      center: [121.59996, 31.197646],
      visible: true,
      tData: [{
          road: '君峰路_金口路',
          result: '失衡',
          type: '主路口',
          button:'生成',
          center:[120.421317,36.170361],
          zoom:18
        }, {
          road: '重庆中路_延寿宫路',
          result: '溢出关联',
          type: '关联路口',
          button:'生成',
        }, {
          road: '银川西路_劲松三路',
          result: '溢出',
          type: '主路口',
          button:'生成',
        }, {
          road: '九水东路_银液泉路_巨峰路',
          result: '失衡',
          type: '主路口',
          button:'生成',
        },
        {
          road: '书院路_京口路',
          result: '溢出',
          type: '主路口',
          button:'生成',
        }],
        gridData: [{
          type: 'A',
          old_time:'50',
          new_time:'53',
          change_time:'3'
        }, {
          type: 'B',
          old_time:'16',
          new_time:'20',
          change_time:'4'
        }, {
          type: 'C',
          old_time:'35',
          new_time:'45',
          change_time:'10'
        }]
    };
  },
  mounted() {
    
    this.initAmap(); 
  },
  
  methods: {
    handleChange(value) {
        console.log(value);},
    handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
    openFullScreen2() {
        const loading = this.$loading({
          lock: false,
          text: '方案生成中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.fa_visible=true;
        }, 2000);
      },
    initAmap() {
      lazyAMapApiLoaderInstance.load().then(() => {
        this.map = new AMap.Map("amapall",  {
		   mapStyle: 'amap://styles/normal',
          center: new AMap.LngLat(120.32756,36.0757),
          pitch: 50, // 地图俯仰角度，有效范围 0 度- 83 度
          zoom: 17,
          rotation: -15,
          viewMode: "3D", //开启3D视图,默认为关闭
          buildingAnimation: true, //楼块出现是否带动画
          expandZoomRange: true,
          zooms: [3, 20],
          features: ["bg", "road", "building","point"],// 这里指想展示什么数据就设置，目前只支持这四种，下图给出了对应字段的意思
        });
      });
    },
    Click(record,index){ 
			AMap.Map.center = record.center
			AMap.Map.zoom = record.zoom
			},
    tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
	selectPoi(e){
      console.log('selectPoi: ', e);
    },
    choosePoi(e){
      console.log('choosePoi: ', e);
    }
  },
};
</script>

<style lang="scss">
  .like {
    cursor: pointer;
    font-size: 25px;
    display: inline-block;
  }
 .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 880px;
  }
.el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
.contains {
	display: flex;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	.all-amap {
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		.search-box{
			padding: auto;
    }
    .road_table_box {
				width: 594px;
				height: 428px;
				border-radius: 2px;
				background-color: #FFFFFF;
        position: absolute;
        top: 50px;
        z-index: 999;
		
				.road_table_title_box {
          position: absolute;
          z-index: 9999;
					height: 47px;
					padding-left: 24px;
					font-size: 16px;
					font-family: PingFang SC;
					font-weight: 400;
					color: #262626;
					text-align: left;
					line-height: 47px;
					box-sizing: border-box;
				}
		
				.table_box {
          padding-top: 47px;
          position: absolute;
          z-index: 999;
					height: 380px;
				}
        .table_box_gap {
				width: 1000px;
				height: 1px;
				background-color: #F0F0F0;
        position: absolute;
        top: 47px;
        z-index: 9999;
			}
			}
	}
}

</style>